<template>
  <div class="feed maxWidthAndCenter">
    <Header />
    <div class="spaceDivider" />
    <h1>Vibbz feed</h1>

    <!-- <p v-if="!postsArr.slice(numberOne, numberTwo).length">End</p> -->

    <PostLayout
      v-for="post in postsArr.slice(numberOne, numberTwo)"
      :key="post"
      :postID="post._id"
      :textEl="post.text"
      :usernameEl="post.username"
      :emotionEl="post.emotion"
      :hashtagsEl="post.hashtags"
      :timestampEL="post.timestamp"
      :showDeleteBtn="false"
    />

    <div class="btnBox">
      <div>
        <button @click="back()" v-if="!numberOne == 0">
          <img src="../assets/iconLeft.png" alt="" />
        </button>
      </div>
      <div>
        <p>{{ pageNumber }} / {{ Math.round(postsArr.length / 5) }}</p>
      </div>
      <div>
        <button
          @click="forward()"
          v-if="pageNumber != Math.round(postsArr.length / 5)"
        >
          <img src="../assets/iconRight.png" alt="" />
        </button>
      </div>
    </div>

    <!-- <Footer /> -->
  </div>
</template>



<script>
import Header from "../components/Header.vue";
import Footer from "../components/Footer.vue";
import PostLayout from "../components/PostLayout.vue";

export default {
  name: "HomeView",

  //Import components
  components: {
    Header,
    Footer,
    PostLayout,
  },

  data() {
    return {
      postsArr: {},
      numberOne: 0,
      numberTwo: 5,
      pageNumber: 1,
    };
  },



  methods: {
    forward() {
      this.numberOne = this.numberOne + 5;
      this.numberTwo = this.numberTwo + 5;
      this.pageNumber++;
      window.scrollTo({
      behavior: "smooth",
      top: 0,
    });
    },

    back() {
      this.numberOne = this.numberOne - 5;
      this.numberTwo = this.numberTwo - 5;
      this.pageNumber--;
      window.scrollTo(0, 0);
    },
    //Get posts
    getPosts() {
      fetch(`http://localhost:3000/posts`, {
        method: "GET",
      }).then((response) => {
        response.json().then((data) => {
          this.postsArr = data;
        });
      });
    },

    getImgUrl(emotion) {
      var images = require.context("../assets/moods", false, /\.png$/);
      return images("./" + emotion + ".png");
    },
  },

  beforeMount() {
    this.getPosts();
  },
};
</script>


