<template>
  <div class="postLayout">
    <div class="img">
      <img :src="getImgUrl(emotionEl)" v-bind:alt="emotionEl" />
    </div>
    <div class="text">
      <div>
        <h3>{{ usernameEl }}</h3>
        <p class="timestamp">
          {{ timestampEL.slice(0, 10) }},
          {{ timestampEL.slice(11, -8) }}
        </p>
      </div>
      <p>
        {{ textEl }}
      </p>
      <ul>
        <li class="hashtags" v-for="hashtag in hashtagsEl" :key="hashtag">
          #{{ hashtag }}
        </li>
      </ul>
    </div>
    <div v-if="showDeleteBtn !== false" class="buttonBox">
      <button @click="deletePost(postID)" title="Delete vibb">
        <img src="../assets/iconTrash.png" alt="" />
      </button>
      <!-- <button @click="editPost(postID)">
        <img src="../assets/iconEdit.png" alt="" />
      </button> -->
    </div>
  </div>
</template>

<script>
export default {
  name: "PostLayout",

  props: {
    postID: {
      type: String,
      default: "Missing",
    },
    emotionEl: {
      type: String,
      default: "glad",
    },
    usernameEl: {
      type: String,
      default: "Missing",
    },
    timestampEL: {
      type: String,
      default: "Missing",
    },
    textEl: {
      type: String,
      default: "Missing",
    },
    hashtagsEl: {
      type: Array,
    },
    showDeleteBtn: {
      type: Boolean,
      default: false,
    },
  },

  methods: {
    //Get posts
    getImgUrl(emotion) {
      var images = require.context("../assets/moods", false, /\.png$/);
      return images("./" + emotion + ".png");
    },

    deletePost(postID) {
      let alertReslut = confirm("Are u sure?");

      if (alertReslut) {
        //Fetch to get uses posts
        fetch(`http://localhost:3000/posts/${postID}`, {
          method: "DELETE",
        }).then((response) => {
          response.json().then((data) => {
            if (response.ok) {
              console.log(data);
              location.reload();
            } else {
              console.log(data);
            }
          });
        });
      }
    },

    editPost(postID) {
      window.location.replace("/#/edit/" + postID);
    },
  },
};
</script>

<style scoped lang="scss">
@import "../style/main.scss";

.postLayout {
  display: flex;
  align-items: center;
  gap: 1em;
  width: $widthSize;
  padding: 1em;
  box-sizing: border-box;
  border: 2px solid $light;
  border-radius: $br;

  margin: 0 auto;
  margin-bottom: 2em;

  position: relative;

  .img {
    width: 100%;
    max-width: 100px;
    text-align: center;

    img {
      background-color: $light;
      border-radius: 50%;

      width: 100px;
      height: 100px;
    }
  }

  .text {
    width: 100%;
    border-radius: $br;
    position: relative;
    padding: 1em;

    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1em;
  }

  .timestamp {
    color: $grey;
    font-size: 0.9em;
    margin-top: -12px;

  }

  .buttonBox {
    position: absolute;
    right: 6px;
    top: 6px;

    display: flex;
    gap: 4.7px;
  }

  button {
    box-sizing: border-box;

    background-color: white;
    border: none;
    cursor: pointer;

    &:hover {
      transform: scale(0.9);
      transition: 0.2s;
    }

    img {
      width: 27.7px;
      height: 27.7px;
    }
  }

  .hashtags {
    color: $grey;
    display: inline;
    margin-right: 0 6.8px;
  }
}

.postLayout:nth-child(2n + 0) {
  flex-direction: row-reverse;
}

@media screen and (max-width: 650px) {
  .postLayout {
    flex-direction: column !important;
    width: 100%;
  }
}
</style>